// 教师引入
import React, { Component } from 'react';
import { Col } from 'antd';
import { getAction } from '../../../axios';
import OtherChart from '../../../common/components/BaseEcharts/OtherChart';
// import jsonData from '../../../data/teacher/教师视角--教师引入.json';

export default class TeacherIntroduce extends Component {
  state={
    indicator: [],
    data: [],
  }
  componentDidMount(){
    this.getData();
  }
  getData(){
    getAction('/bigdata/teacher/info/condition/queryTeacherIntroduce').then(res => {
      if(res.success){
        let indicator = [];
        res.obj.axis && res.obj.axis.forEach(item => {
          indicator.push({
            name: item,
            max: 100
          })
        })
        this.setState({
          indicator,
          data: res.obj.data,
        })
      }
    })
  }
  render() {
    let {data, indicator} = this.state
    let option = {
      title: {
        show: false,
      },
      grid: {
        top: '50%',
        // left: '3%',
        // right: '3%',
        // bottom: '25%',
        containLabel: false,
      },
      tooltip: {},
      legend: {
        show: false,
      },
      radar: {
        shape: 'circle',
        center: ['50%', '55%'],
        nameGap :0,
        axisLine: {
          lineStyle: {
            color: '#00CFFF',
          },
        },
        name: {
          textStyle: {
            color: '#00CFFF',
            borderRadius: 3,
            padding: [0, 0],
          },
        },
        indicator,
      },
      series: [{
        name: '',
        type: 'radar',
        areaStyle: {
          normal: {
            color: '#00CFFF',
          },
        },
        lineStyle: {
          color: '#00CFFF',
        },
        symbolSize:0,
        data: [
          {
            value: data,
            name: '',
          },
        ],
      }],
    };
    return (
      <Col span={24} style={{height:'100%'}}>
        <OtherChart
          id='TeacherIntroduce'
          data={option.radar.indicator[0]?option:null}
          height='100%'/>
      </Col>
    );
  }
}
