// 参军入伍
import React, {Component} from 'react';
import {Col} from 'antd';
import '../styles.less'
import { getAction } from '../../../axios';
import LineEcharts from '../../../common/components/BaseEcharts/LineEcharts';
// import jsonData from '../../../data/teacher/教师视角--教师发展--科研项目.json'

export default class ResearchProject extends Component {
  state={
    axis: [],
    data: [],
  }
  componentDidMount(){
    this.getData();
  }
  getData(){
    getAction('/bigdata/teacher/info/condition/queryTeacherScientificResearchProjectNum').then(res => {
      if(res.success){
        this.setState({
          axis: res.obj.axis,
          data: res.obj.data,
        })
      }
    })
  }
  render(){
    let option = {
      title: {
        show: false
      },
      tooltip: {
        trigger: 'axis'
      },
      legend: {
        show:false
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '5%',
        top:'6%',
        containLabel: true
      },
      toolbox: {
        feature: {
          saveAsImage: {}
        }
      },
      xAxis: {
        axisLabel: {
          interval:0,
          textStyle: {
            color: '#00CFFF'
          }
        },
        data: this.state.axis
      },
      yAxis: {
        type:'value',
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          color:'#00CFFF'
        },
        splitLine:{
          // show:false,
          lineStyle: {
            color: "#379fb7",
            type: "dashed"
          }
        }
      },
      color:['#30CCF4','#DED324','#EE6AE6'],
      series: [
        {
          name:'',
          type:'line',
          data: this.state.data,
          areaStyle: {}
        }
      ]
    };

    return(
      <Col span={24} style={{height:'100%'}}>
        <LineEcharts
          id='ResearchProjectLine'
          data={option}
          height='100%'/>
      </Col>
    )
  }
}
