// 教师学历
import React, {Component} from 'react';
import {Col} from 'antd';
import { getAction } from '../../../axios';
import PieEcharts from '../../../common/components/BaseEcharts/PieEcharts';
// import jsonData from '../../../data/teacher/教师视角--教师培养--教师学历.json';

export default class TeacherEducation extends Component {
  state={
    data: [],
  }
  componentDidMount(){
    this.getData();
  }
  getData(){
    getAction('/bigdata/teacher/info/condition/queryTeacherEducationDistributionInfo').then(res => {
      if(res.success && res.obj[0]){
        this.setState({
          data: res.obj
        })
      }
    })
  }
  render(){
    let { data } = this.state;
    let all = 0;
    data.forEach((il) => {
      if(typeof il.value!=='number'){
        il.value = Number(il.value);
      }
      all = il.value+all;
    });
    let option = {
        title: {
          show:false
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)',
        },
        legend: {
          orient: 'vertical',
          x: '60%',
          y: 'center',
          icon: 'circle',
          itemWidth: 5,
          itemHeight: 5,
          itemGap: 13,
          textStyle: {
            color: '#00CFFF',
          },
          formatter: (name) => {
            let cur = data.filter(item=>item.name===name)[0];
            let persent = Math.round((Number(cur.value)/all) * 100) + "%";
            return name + ":" + cur.value + `(${persent})`;
          },
        },
        color: ['#00CFFF', '#04FFC7', '#65D62D', '#F8E71C', '#FF2C2C'],
        series: [
          {
            name: '',
            type: 'pie',
            radius : '55%',
            center: ['40%', '50%'],
            label: {
              normal: {
                show: false,
                position: 'center',
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: '30',
                  fontWeight: 'bold',
                },
              },
            },
            labelLine: {
              normal: {
                show: false,
              },
            },
            data: data,
          },
        ],
      };

    return(
      <Col span={24} style={{height:'100%'}}>
        <PieEcharts
          id='TeacherEducationPie'
          data={option}
          height='100%'/>
      </Col>
    )
  }
}
