import React, { Component } from 'react'
import { Carousel} from 'antd'
import GrowthIndex from './components/GrowthIndex'
import TeacherNum from './components/TeacherNum';
import ResearchProject from './components/ResearchProject';
import GetCertificate from './components/GetCertificate';
import FurtherCost from './components/FurtherCost';
import TeacherEducation from './components/TeacherEducation';
import PracticePersonNum from './components/PracticePersonNum';
import TitlePassRate from './components/TitlePassRate';
import TeacherDevelopment from './components/TeacherDevelopment';
import PartTimeJob from './components/PartTimeJob';
import BorrowingBook from './components/BorrowingBook';
import TeacherIntroduce from './components/TeacherIntroduce';
export default class Jiaoshi extends Component {
  render() {
    let autoplaySpeed = 40000;
        const settings = {
            // autoplay: true,
            autoplaySpeed,
            dots: true,
            infinite: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            vertical: true,
            verticalSwiping: true,
            beforeChange: function (currentSlide, nextSlide) {
                // console.log("before change", currentSlide, nextSlide);
            },
            afterChange: (currentSlide) => {
                if (currentSlide === 4) {
                    setTimeout(() => {
                        if (this.slider && this.slider.goTo) {
                            this.slider.goTo(0)
                        }
                    }, autoplaySpeed)
                }
            },
        };
    return (
      <Carousel ref={slider => (this.slider = slider)} {...settings} >
        <div>          
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教师发展-成长指数
                            </span>

              </div>
              <div className='item-chart'>
              <GrowthIndex></GrowthIndex>
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教师发展-教师数量
                            </span>

              </div>
              <div className='item-chart'>
              <TeacherNum></TeacherNum>
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教师发展-科研项目
                            </span>

              </div>
              <div className='item-chart'>
              <ResearchProject></ResearchProject>
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                证书获取
                            </span>

              </div>
              <div className='item-chart'>
              <GetCertificate></GetCertificate>
              </div>              
            </div>
          </div>    

          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教师培养-教师学历
                            </span>

              </div>
              <div className='item-chart'>
              <TeacherEducation></TeacherEducation>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教师培养-进修费用
                            </span>

              </div>
              <div className='item-chart'>
              <FurtherCost></FurtherCost>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教师培养-出国/实践人数
                            </span>

              </div>
              <div className='item-chart'>
              <PracticePersonNum></PracticePersonNum>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教师培养-职称通过率
                            </span>

              </div>
              <div className='item-chart'>
              <TitlePassRate></TitlePassRate>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教师发展
                            </span>

              </div>
              <div className='item-chart'>
              <TeacherDevelopment/>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                社会兼职
                            </span>

              </div>
              <div className='item-chart'>
              <PartTimeJob/>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                图书借阅
                            </span>

              </div>
              <div className='item-chart'>
              <BorrowingBook/>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教师引入
                            </span>

              </div>
              <div className='item-chart'>
              <TeacherIntroduce/>
              </div>              
            </div>
          </div>        
        </div>
        {/* 每一页的划分 */}        
        
      </Carousel>
    )
  }
}
