// 图书借阅
import React, {Component} from 'react';
import {Col} from 'antd';
import { getAction } from '../../../axios';
import PieEcharts from '../../../common/components/BaseEcharts/PieEcharts';
// import jsonData1 from '../../../data/teacher/教师视角--图书借阅.json';

export default class BorrowingBook extends Component {
  state={
    data: [],
  }
  componentDidMount(){
    this.getData();
  }
  getData(){
    getAction('/bigdata/teacher/info/condition/queryBorrowBooksProportion').then(res => {
      if(res.success && res.obj[0]){
        this.setState({
          data: res.obj
        })
      }
    })
  }
  render(){
    let {data} = this.state;
    let all = 0;
    data.forEach((il) => {
      if(typeof il.value!=='number'){
        il.value = Number(il.value);
      }
      all = il.value+all;
    });
    let option = {
        title: {
          show:false
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)',
        },
        legend: {
          orient: 'vertical',
          x: '55%',
          y: 'center',
          icon: 'circle',
          itemWidth: 5,
          itemHeight: 5,
          itemGap: 13,
          textStyle: {
            color: '#00CFFF',
          },
          formatter: (name) => {
            let cur = data.filter(item=>item.name===name)[0];
            let persent = Math.round((Number(cur.value)/all) * 100) + "%";
            return name + ":" + cur.value + `(${persent})`;
          },
        },
        color: ['#00CFFF', '#13C2C2', '#2FC25B', '#FACC14', '#F04864'],
        series: [
          {
            name: '',
            type: 'pie',
            center: ['30%', '50%'],
            radius: ['50%', '70%'],
            avoidLabelOverlap: false,
            label: {
              color: '#00CFFF',
              normal: {
                show: false,
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: '30',
                  fontWeight: 'bold',
                },
              },
            },
            labelLine: {
              normal: {
                show: false,
              },
            },
            data: data,
          },
        ],
      };
    return(
      <Col span={24} style={{height:'100%'}}>
        <PieEcharts
          id='BorrowingBookPie'
          data={option}
          height='100%'/>
      </Col>
    )
  }
}
