// 证书获取
import React, {Component} from 'react';
import {Col} from 'antd';
import { getAction } from '../../../axios';
import BarEcharts from '../../../common/components/BaseEcharts/BarEcharts';
// import jsonData from '../../../data/teacher/教师视角--证书获取.json';

export default class GetCertificate extends Component {
  state={
    axis: [],
    data: [],
  }
  componentDidMount(){
    this.getData();
  }
  getData(){
    getAction('/bigdata/teacher/info/condition/queryTeacherCertificateCircumstance').then(res => {
      if(res.success){
        this.setState({
          axis: res.obj.axis,
          data: res.obj.data,
        })
      }
    })
  }
  render(){
    let option = {
      title: {
        show:false
      },
      xAxis: {
        data: this.state.axis,
        axisLabel: {
          interval:0,
          textStyle: {
            color: '#00CFFF'
          }
        }
      },
      color:['#00CFFF'],
      yAxis: {
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          show:false
        },
        splitLine:{
          // show:false,
          lineStyle: {
            color: "#379fb7",
            type: "dashed"
          }
        }
      },
      series: [
        {
          type: 'bar',
          data: this.state.data,
          label: {
            normal: {
              color:'#fff',
              show: true,
              position: 'top'
            }
          },
        }
      ]
    };
    return(
      <Col span={24} style={{height:'100%'}}>
        <BarEcharts
          id='GetCertificateBar'
          data={option}
          height='100%'/>
      </Col>
    )
  }
}
