// 成长指数
import React, { Component } from 'react';
import { Row, Col } from 'antd';
import '../styles.less'
import { getAction } from '../../../axios';
// import avg from '../../../data/teacher/教师视角--教师发展--成长指数--全校平均指数.json'
// import teacherList from '../../../data/teacher/教师视角--教师发展--成长指数--具体教师.json'
export default class SelectionSituation extends Component {
  constructor(props){
    super(props);
    this.state = {
      // index:avg.RECORDS[0].comprehensiveIndex,
      // data:teacherList.RECORDS,
      index:0,
      data:[],
    }
  }
  componentDidMount(){
    this.getData();
  }
  getData(){
    getAction('/bigdata/teacher/info/condition/teacherGrowthIndexSituation').then(res => {
      if(res.success){
        this.setState({
          index: res.obj.left.comprehensiveIndex,
          data: res.obj.right
        })
      }
    })
  }
  render() {
    return (
      <Row type="flex" align="middle" style={{height:"100%",textAlign:"center",color:"#fff"}}>
        <Col span={10}>
          <div className={'teacher_visual_growth_index_num'}>{this.state.index}%</div>
          <div className={'teacher_visual_growth_index_label'}>全校平均指数</div>
        </Col>
        <Col span={14}>
            {this.state.data.map((il,i) => {
                return <div key={i}>
                    <span className={'teacher_visual_growth_index_index'}>{i+1}</span>
                    <span className={'teacher_visual_growth_index_name'}>{il.teacherName}</span>
                    <span className={'teacher_visual_growth_index_value'}>{il.abilityScore}</span>
                </div>
            })}
        </Col>
      </Row>
    )
  }
}
