// 实践人数
import React, { Component } from 'react';
import { Col } from 'antd';
import { getAction } from '../../../axios';
import BarEcharts from '../../../common/components/BaseEcharts/BarEcharts';
// import jsonData from '../../../data/teacher/教师视角--教师培养--出国实践人数.json';

export default class PracticePersonNum extends Component {
  state={
    axis: [],
    practice: [],
    open: []
  }
  componentDidMount(){
    this.getData();
  }
  getData(){
    getAction('/bigdata/teacher/info/condition/queryTeacherTrainingPractice').then(res => {
      if(res.success){
        this.setState({
          axis: res.obj.axis,
          practice: res.obj.practice,
          open: res.obj.open,
        })
      }
    })
  }
  render() {
    let option = {
      title: {
        show: false,
      },
      xAxis: {
        data: this.state.axis,
        axisLabel: {
          interval: 0,
          textStyle: {
            color: '#00CFFF',
          },
        },
      },
      legend: {
        icon: 'circle',
        itemWidth: 14,
        itemHeight: 5,
        itemGap: 13,
        bottom: '4%',
        textStyle: {
          fontSize: 12,
          color: '#00cfff'
        }
      },
      grid: {
        top: '10%',
        left: '3%',
        right: '3%',
        bottom: '15%',
        containLabel: true
      },
      color: ['#00CFFF','#BD10E0'],
      yAxis: {
        axisLine: {
          show: false,
        },
        axisTick: {
          show: false,
        },
        axisLabel: {
          show: false,
        },
        splitLine: {
          lineStyle: {
            color: '#379fb7',
            type: 'dashed',
          },
        },
      },
      series: [
        {
          name:'实训项目数',
          type: 'bar',
          barGap: 0,
          data: this.state.practice,
          label: {
            normal: {
              color: '#fff',
              show: true,
              position: 'top',
            },
          },
        },
        {
          name:'开出项目数',
          type: 'bar',
          barGap: 0,
          data: this.state.open,
          label: {
            normal: {
              color: '#fff',
              show: true,
              position: 'top',
            },
          },
        },
      ],
    };
    return (
      <Col span={24} style={{height:'100%'}}>
        <BarEcharts
          id='PracticePersonNumBar'
          data={option}
          height='100%'/>
      </Col>
    );
  }
}
